body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Universal Styles */

html {
  font-size: 18px;
}

@media only screen and (max-width: 560px) {
  html {
    font-size: 14px;
  }
}

input:focus {
  outline: none;
}

/* Reusable Component - Image Container */

.image-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
}

.image-container img {
  width: 100%;
}

/* App Styles */
.App {
  font-family: 'Work Sans', sans-serif;
}

h1 {
  padding: .6rem 0;
  text-align: center;
  background-color: #dbab4b;
  font-family: Poppins, sans-serif;
  font-size: 1.6rem;
  color: #fff;
  margin: 0 0;
}
.BusinessList {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 4.8rem 4%;
  }

  @media only screen and (max-width: 480px) {
    .BusinessList {
      margin: 4.4rem 0%;
    }
  }
.Business {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 20rem;
    margin: 0 .5rem 2.3rem .5rem;
    border: 2px solid black;
    border-radius: 9px;
    padding: 13px 16px;
    box-shadow: -5px 5px 15px rgb(10, 10, 10);
    transition: margin 0.2s;
  }
  
.image-container {
  height: 29rem;
}

.image-container img {
  height: 15.66rem;
  margin-bottom: 1rem;
  margin: 0 0;
  padding: 0 0;
  border-radius: 5px;
}

.Business:hover  {
   box-shadow: -8px 8px 20px rgb(10, 10, 10);
}

.Business h2 {
  margin-bottom: .5rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.Business-information {
  display: flex;
  justify-content: space-between;
}

.Business-information p {
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.3rem;
  margin: 3px 0;
  padding: 0 0;
}

.Business-address {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 0;
  padding: 10px 0;
}

.Business-reviews {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
  margin: 0 0;
  padding: 10px 0;
}

.Business-reviews h3 {
  color: #CCA353;
  font-weight: 600;
  margin: 0 0;
  padding: 0 0;
}

.Business-reviews .rating {
  font-size: 1rem;
}

.image-container a {
  text-align: center;
  text-decoration: none;
}

.image-container a:hover {
  text-decoration: underline;
  font-size: 1.05rem;
  color: rgb(0, 0, 255);
  transition-duration: .4s;
}
.SearchBar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: url(/search-restaurants/static/media/background_search_desktop.902df4c5.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    height: 23rem;
    box-shadow: -8px 8px 15px #000000;
  }
  
  .SearchBar-sort-options ul {
    display: flex;
    justify-content: center;
    text-align: center;
    margin-bottom: 2.22rem;
    color: #ffffff;
    padding: 0 0;
  }
  
  .SearchBar-sort-options li {
    display: flex;
    justify-content: center;
    cursor: pointer;
    width: 4.33rem;
    border-bottom: 1px solid #fff;
    padding: 0 2.58rem .33rem 2.58rem;
    line-height: 1.13;
    font-weight: 600;
    font-size: .93rem;
    transition: color .5s;
    list-style-type: none;
  }
  
  .SearchBar-sort-options li:hover {
    color: #fab326;
  }
  
  .SearchBar-sort-options li.active,
  .SearchBar-sort-options li.active:hover {
    border-bottom: 1px solid #f0c36c;
    color: #f0c36c;
  }
  
  .SearchBar-fields {
    display: flex;
    justify-content: center;
    margin-bottom: 2.88rem;
  }

  .SearchBar-fields input:focus {
    box-shadow: -8px 8px 15px rgb(7, 7, 7);
    border: 2px solid rgb(31, 31, 241);
  }
  
  .SearchBar-fields form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;    
  }
  
  .SearchBar-fields input {
    width: 20rem;
    padding: .66rem 1rem;
    margin: 0px 2rem;
    border-radius: 6px;
    font-size: .77rem;
    font-weight: 500;
    justify-content: center;
  }

  .SearchBar-submit {
    text-align: center;
  }
  
  .SearchBar-submit button {
    border-radius: 4px;
    padding: .72rem 1.7rem;
    background-color: #cca353;
    color: #ffffff;
    font-weight: 600;
    transition: background-color .5s;
    border: none;
  }
  
  .SearchBar-submit button:hover {
    cursor: pointer;
    background-color: #a7874b;
  }

  .inputFieldError {
    border: 2px solid red;
    transition: border 0.3s;
  }

  .inputFieldErrorMessage {
    display: flex;
    justify-content: center;
    align-items: center;
    color: red;
    font-size: 1.1em;
    width: 300px;
    height: 30px;
    margin: 0 auto;
    font-weight: bold;
  }
  
  @media only screen and (max-width: 890px) {
    .SearchBar {
      background-image: url(/search-restaurants/static/media/background_search_mobile.5d0905e6.jpg);
    }
  
    .SearchBar-sort-options ul {
      margin-left: 2rem;
      margin-right: 2rem;
    }

    .SearchBar-sort-options li {
      width: 1.8rem;
      border-bottom: 1px solid #fff;
      padding: 0 2.58rem .33rem 2.58rem;
      line-height: 1.13;
      font-weight: 600;
      font-size: .93rem;
      transition: color .5s;
      list-style-type: none;
    }
  
    .SearchBar-fields {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }

    .SearchBar-fields form {
      text-align: center;
      margin: 0 auto;
    }
  
    .SearchBar-fields input {
      margin-right: 0;
      margin-bottom: .86rem;
      width: 16rem;
      padding: .66rem 1rem;
      margin: 0px 2rem;
    }
    .inputFieldErrorMessage {
      color: red;
      padding: 0px 5px;
      font-size: 1.1em;
    }
  }
.container {
    display: flex;
    height: 1000px;
    justify-content: center;
}
.spinner {
    display: flex;
    justify-content: center;
}
.error {
    display: flex;
    margin: 5px 10px;
    color: red;
    justify-content: center;
    align-content: center;
    align-items: center;
    font-size: 1.5em;
}

h3.error {
    text-align: center;
}
